*{
    background-color: #F9F9F9;
    margin: auto;
    padding: 0;
}
h1{
    user-select: none;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    color: blueviolet;
    font-size: 55px;
    font-style: inherit;
    margin-top: 5%;
    font-weight: 1000;
}
h3{
    user-select: none;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(0, 0, 0);
    font-size: 35px;
    font-style: inherit;
    margin-top: 11%;
background-color: white;
    font-weight: 1000;
}
#BOX{
    margin-top:2%;
    border: 1px solid white;
    border-radius: 20px;
    background-color: white;
    height: 300px;
    width: 500px;
}
input{
    cursor: pointer;
    outline: none;
    border: none;
   border-bottom: 1px solid black;
    height: 26px;
    width: 240px;
    background-color: white;
    align-self: center;
    padding-left: 5px;
    margin-left: 50%;
    margin-top: 5%;
    transform: translateX(-50%);
}

button{
    cursor: pointer;
    outline: none;
    border: 1px solid blueviolet;
    background-color: #F9F9F9;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    height: 26px;
    width: 230px;
    align-self: center;
    padding-left: 5px;
    color: blueviolet;
    margin-left: 50%;
    margin-top: 5%;
    transform: translateX(-50%);
}
button:hover{
    
    background-color: blueviolet;
    color: #F9F9F9;
}


#btn{
cursor: pointer;
}